html {
	font-size: var(--font-base-size);
}

.edit-post-visual-editor .editor-styles-wrapper {
	color: var(--color-text);
    font-family: var(--font-family-body);
    font-size: var(--font-size-body);
    font-weight: var(--font-weight-body);
    line-height: var(--line-height-body);
    letter-spacing: var(--letter-spacing-body);
}

.edit-post-visual-editor .editor-styles-wrapper h1,
.edit-post-visual-editor .editor-styles-wrapper h2,
.edit-post-visual-editor .editor-styles-wrapper h3,
.edit-post-visual-editor .editor-styles-wrapper h4,
.edit-post-visual-editor .editor-styles-wrapper h5,
.edit-post-visual-editor .editor-styles-wrapper h6 {
	color: var(--color-headings);
    font-family: var(--font-family-headings);
    font-weight: var(--font-weight-headings);
    line-height: var(--line-height-headings);
    letter-spacing: var(--letter-spacing-headings);
	margin-top: var(--font-spacing-4);
	margin-bottom: var(--font-spacing-4);
}

.edit-post-visual-editor .editor-styles-wrapper h1 {
	font-size: var(--font-size-h1);
}

.edit-post-visual-editor .editor-styles-wrapper h2 {
	font-size: var(--font-size-h2);
}

.edit-post-visual-editor .editor-styles-wrapper h3 {
	font-size: var(--font-size-h3);
}

.edit-post-visual-editor .editor-styles-wrapper h4 {
	font-size: var(--font-size-h4);
}

.edit-post-visual-editor .editor-styles-wrapper h5 {
	font-size: var(--font-size-h5);
}

.edit-post-visual-editor .editor-styles-wrapper h6 {
	font-size: var(--font-size-h6);
}

.edit-post-visual-editor .editor-styles-wrapper h1:first-child,
.edit-post-visual-editor .editor-styles-wrapper h2:first-child,
.edit-post-visual-editor .editor-styles-wrapper h3:first-child,
.edit-post-visual-editor .editor-styles-wrapper h4:first-child,
.edit-post-visual-editor .editor-styles-wrapper h5:first-child,
.edit-post-visual-editor .editor-styles-wrapper h6:first-child { 
	margin-top: 0 !important; 
}

.edit-post-visual-editor .editor-styles-wrapper p {	
	margin-bottom: var(--font-spacing-unit); 
}

.edit-post-visual-editor .editor-styles-wrapper strong { 
	font-weight: bolder; 
}

.edit-post-visual-editor .editor-styles-wrapper em { 
	font-style: italic; 
}

.edit-post-visual-editor .editor-styles-wrapper small { 
	font-size: var(--font-size-2); 
}

.edit-post-visual-editor .editor-styles-wrapper big { 
	font-size: var(--font-size-4); 
}

.edit-post-visual-editor .editor-styles-wrapper sub { 
	font-size: var(--font-size-1);
	vertical-align: sub; 
}

.edit-post-visual-editor .editor-styles-wrapper sup { 
	font-size: var(--font-size-1);
	vertical-align: super; 
}

.edit-post-visual-editor .editor-styles-wrapper abbr[title] {  
	border-bottom: 1px dotted var(--color-text); 
	cursor: help;
}

.edit-post-visual-editor .editor-styles-wrapper address { 
	display: block; 
	margin-bottom: var(--font-spacing-4); 
}

.edit-post-visual-editor .editor-styles-wrapper blockquote {
	overflow: hidden;
}

.edit-post-visual-editor .editor-styles-wrapper pre {
	display: block;
	margin-bottom: var(--font-spacing-4); 
	overflow-x: auto; 
	white-space: pre-wrap;
}

.edit-post-visual-editor .editor-styles-wrapper a {
	color: var(--color-link);
	text-decoration: none;
	outline: 0;
	transition: color 0.25s;
	text-decoration: none;
}

.edit-post-visual-editor .editor-styles-wrapper a:focus { color: var(--color-link); }
.edit-post-visual-editor .editor-styles-wrapper a:hover { color: var(--color-link-hover); }
.edit-post-visual-editor .editor-styles-wrapper a:active { color: var(--color-link-active); }	