/* ==========================================================================
   =Gutenberg Block Styling
   ========================================================================== */
   
	/* Search */

	.wp-block-search__button {
		position: relative;
		display: inline-block;
		font: inherit;
		padding: var(--font-spacing-2) var(--font-spacing-4);
		border: 1px solid var(--color-accent-1);
		border-radius: var(--border-radius-buttons);
		margin-bottom: var(--font-spacing-4);
		background-color: var(--color-accent-1);
		color: #fff;
		text-align: center;
		text-decoration: none;
		cursor: pointer;
		appearance: none;
	}

	/* Separator */

	.wp-block-separator.is-style-dots::before {
		color: var(--border-color);
	}

	.wp-block-separator:not(.is-style-dots) {
		border-bottom: 0;
	}

	/* Pull quote*/

	.wp-block-pullquote {
		border-top: 1px solid var(--border-color);
		border-bottom: 1px solid var(--border-color);
		margin-bottom: var(--font-spacing-unit);
		color: currentColor;
		padding: var(--font-spacing-3);
		font-size: var(--font-size-4);
		line-height: var(--line-height-body);
	}

	.wp-block-pullquote__citation, 
	.wp-block-pullquote cite, .wp-block-pullquote footer {
		color: var(--color-muted);
	}

	@media (min-width: 992px) {
		
		.wp-block-pullquote {
			padding: var(--font-spacing-5);
		}
		
	}

	/* Blockquote */

	.wp-block-quote {
		border-left: 0;
		margin: 0 0 var(--font-spacing-4) 0;
		padding-left: 0;
		border-top: 1px solid var(--border-color);
		border-bottom: 1px solid var(--border-color);
		margin-bottom: var(--font-spacing-unit);
		color: currentColor;
		padding: var(--font-spacing-3);
		font-size: var(--font-size-4);
		line-height: var(--line-height-body);
	}

	.wp-block-quote.has-text-align-right {
		border-right: 0;
		padding: var(--font-spacing-3);
	}

	.wp-block-quote cite, 
	.wp-block-quote footer {
		color: var(--color-muted);
	}

	@media (min-width: 992px) {
		
		.wp-block-quote {
			padding: var(--font-spacing-5);
		}
		
	}

	/* Gallery */

	.wp-block-gallery {
		margin-bottom: var(--font-spacing-4); 
	}

	.wp-block-gallery img {
		border-radius: var(--border-radius-default);
	} 

	/* Image */

	.wp-block-image.has-custom-border img, 
	.wp-block-image img {
		border-radius: var(--border-radius-default);	
	}

	/* Align Wide and Full */

	@media (min-width:992px) and (max-width: 1399.98px) {
		
		.alignfull,
		.alignwide {
			width: 100vw;
			max-width: calc( 100vw - var(--spacing-8) );
			margin-left: calc( -50vw + var(--spacing-5) + 50% ) !important;
		}
		
		.rtl .alignfull, 
		.rtl .alignwide {
			margin-left: auto !important;
			margin-right: calc( -50vw + var(--spacing-5) + 50% ) !important;
		}
		
	}

	@media (min-width: 1400px) {
		
		.alignfull,
		.alignwide {
		  width: var(--max-width-content);
		  max-width: var(--max-width-content);
		  margin-left: calc( -1 * var(--max-width-content) / 2 + 50% ) !important;
		}
		
		.rtl .alignfull, 
		.rtl .alignwide {
			margin-left: auto !important;
			margin-right: calc( -1 * var(--max-width-content) / 2 + 50% ) !important;
		}
		
	}

	.rb-has-sidebar .alignfull,
	.rb-has-sidebar .alignwide {
		width: 100% !important;
		max-width: 100% !important;
		margin-left: 0 !important;
	}

	/* Button */

	.editor-styles-wrapper .wp-block-button__link:not(.is-style-outline){
		background-color: var(--color-accent-1) !important;
		color: #ffffff !important;	
		border-radius: var(--border-radius-buttons);
	}

	.wp-block-button.is-style-outline > .wp-block-button__link, 
	.wp-block-button .wp-block-button__link.is-style-outline,
	.wp-block-button__link {
		padding: var(--font-spacing-2) var(--font-spacing-4) !important;
		background-color: var(--color-accent-1);
		color: #ffffff;	
		border-width: 2px !important;
		border-radius: var(--border-radius-buttons);
	}

	.wp-block-button.is-style-outline > .wp-block-button__link, 
	.wp-block-button .wp-block-button__link.is-style-outline {
		padding: calc( var(--font-spacing-2) - 2px ) var(--font-spacing-4) !important;
	}

	.wp-block-button__link:hover {
		border-color: var(--color-accent-2) !important;
		background-color: var(--color-accent-2) !important;
		color: #ffffff !important;
	}