/* ==========================================================================
   =CSS Variables
   ========================================================================== */
	
	:root {

		// Layout	

		--max-width-content: 1230px; 															
		--max-width-content-fullwidth: 100%;
		--max-width-content-no-sidebar: 820px;													// maximum 940px
		
		--sticky-offset: 80px;																	// sticky sidebar, floating share top value, so sticky header doesn't cover them
		
		// Misc
		
		--border-radius-default: 4px;
		--border-radius-inputs: 4px;
		--border-radius-buttons: 4px;

		// Colors

		--color-accent-1: #2E2D4D;
		--color-accent-2: #D50747;
		--color-muted: #777;

		--color-text: #000000;
		--color-headings: #111111;
		
		--border-color: #ddd;
		--background-color: #f4f4f4; 

		--color-form-elements: #000000;
		--color-form-placeholders: #94979e;
		--color-form-labels: #45464b;
		
		--background-color-form-elements: #ffffff;
		--background-color-form-elements-disabled: #f4f4f4;
		
		--border-color-form-elements: #ddd;
		--border-color-form-elements-focus: #bbb;
			
		--color-link: #2E2D4D;
		--color-link-hover: #D50747;
		--color-link-active: #D50747;
		
		// Typography

		--font-base-size: 17px; 																// applied to html element, use for rem values
		--font-scale: 1.067; 																	// 1.067 – Minor Second, 1.125 – Major Second
		
		--font-family-body: 'DM Sans', sans-serif;
		--font-size-body: 1rem;
		--font-weight-body: 400;
		--line-height-body: 1.765;
		--letter-spacing-body: 0;
		
		--font-size-1: calc( var(--font-size-body) / var(--font-scale) / var(--font-scale) ); 	// smallest 
		--font-size-2: calc( var(--font-size-body) / var(--font-scale) );						// small 
		--font-size-3: var(--font-size-body); 													// body, p, h6 
		--font-size-4: calc( var(--font-size-3) * var(--font-scale) ); 							// h5 
		--font-size-5: calc( var(--font-size-4) * var(--font-scale) ); 							// h4 
		--font-size-6: calc( var(--font-size-5) * var(--font-scale) ); 							// h3 
		--font-size-7: calc( var(--font-size-6) * var(--font-scale) ); 							// h2 
		--font-size-8: calc( var(--font-size-7) * var(--font-scale) ); 							// h1 
		--font-size-9: calc( var(--font-size-8) * var(--font-scale) );
		--font-size-10: calc( var(--font-size-9) * var(--font-scale) );	
		--font-size-11: calc( var(--font-size-10) * var(--font-scale) );			

		--font-family-headings: 'Poppins', sans-serif;
		--font-weight-headings: 700;
		--line-height-headings: 1.3;
		--letter-spacing-headings: 0.4px;
		
		--font-size-h1: var(--font-size-8);
		--font-size-h2: var(--font-size-7);
		--font-size-h3: var(--font-size-6);
		--font-size-h4: var(--font-size-5);
		--font-size-h5: var(--font-size-4);
		--font-size-h6: var(--font-size-3);
		
		--font-spacing-unit: calc( var(--font-size-body) * var(--line-height-body) );
		
		--font-spacing-1: calc( var(--font-spacing-unit) / 8 ); 
		--font-spacing-2: calc( var(--font-spacing-unit) / 4 );
		--font-spacing-3: calc( var(--font-spacing-unit) / 2 );
		--font-spacing-4: var(--font-spacing-unit);
		--font-spacing-5: calc( var(--font-spacing-unit) * 1.5 );
		--font-spacing-6: calc( var(--font-spacing-unit) * 2 );
		--font-spacing-7: calc( var(--font-spacing-unit) * 2.5 );
		--font-spacing-8: calc( var(--font-spacing-unit) * 3 );
		
		// Spacing
		
		--spacing-unit: calc( var(--font-size-body) * var(--line-height-body) );

		--spacing-1: calc( var(--spacing-unit) / 8 ); 
		--spacing-2: calc( var(--spacing-unit) / 4 );
		--spacing-3: calc( var(--spacing-unit) / 2 );
		--spacing-4: var(--spacing-unit);
		--spacing-5: calc( var(--spacing-unit) * 1.5 );
		--spacing-6: calc( var(--spacing-unit) * 2 );
		--spacing-7: calc( var(--spacing-unit) * 2.5 );
		--spacing-8: calc( var(--spacing-unit) * 3 );
		--spacing-9: calc( var(--spacing-unit) * 3.5 );
		--spacing-10: calc( var(--spacing-unit) * 4 );
		--spacing-11: calc( var(--spacing-unit) * 4.5 );
		--spacing-12: calc( var(--spacing-unit) * 5 );
		
		// Body
		
		--background-color-body: #ffffff;
		
		// Header Top
		
		--background-color-header-top: var(--background-color-body);
		
		// Cta 
		
		--color-cta: #ffffff; 
		--color-cta-hover: #ffffff;
		
		
		--font-family-cta: 'DM Sans', sans-serif;
		--font-weight-cta: 700;
		
		--background-color-cta: var(--color-accent-1);
		--border-color-cta: var(--color-accent-1);
		
		--background-color-cta-button: var(--color-accent-2);
		--color-cta-button: #ffffff;
		
		// Header 
		
		--background-color-header: var(--background-color-body);
		--z-index-header-sticky: 9999;
		--box-shadow-header: 0px 0px 15px #0000001a;
		
		--color-logo: var(--color-accent-2);
		
		--font-family-logo: 'DM Sans', sans-serif;
		--font-weight-logo: 700;
		
		--letter-spacing-logo: -1.5px;
		
		--font-size-logo-mobile: var(--font-size-11);
		--font-size-logo-tablet: var(--font-size-11);
		--font-size-logo-desktop: var(--font-size-11);
		
		--font-size-logo-mobile-sticky: var(--font-size-11);
		--font-size-logo-tablet-sticky: var(--font-size-9);
		--font-size-logo-desktop-sticky: var(--font-size-9);
		
		--max-height-logo-mobile: var(--spacing-6);
		--max-height-logo-tablet: var(--spacing-7);
		--max-height-logo-desktop: var(--spacing-8);
		
		--max-height-logo-mobile-sticky: var(--spacing-6);
		--max-height-logo-tablet-sticky: var(--spacing-6);
		--max-height-logo-desktop-sticky: var(--spacing-5);

		--color-header-icons: var(--color-text); 
		--color-header-icons-hover: var(--color-accent-2);
		
		--font-size-header-icons-mobile: var(--font-size-8);
		--font-size-header-icons-tablet: var(--font-size-7);
		--font-size-header-icons-desktop: var(--font-size-7);
		
		--color-menu: var(--color-text);
		--color-menu-hover: var(--color-accent-2);
		
		--text-transform-menu: uppercase;
		--font-size-menu: var(--font-size-3);
		--line-height-menu: 2.75;
		--line-height-menu-sticky: 2;
		
		--font-family-menu: 'DM Sans', sans-serif;
		--font-weight-menu: 700;
		
		--spacing-menu: var(--spacing-3);
		--logo-padding-bottom: var(--spacing-5);
		
		--color-submenu: var(--color-text); 
		--color-submenu-hover: var(--color-accent-2);
		--background-color-submenu: #ffffff;  
		--border-color-submenu: transparent;
		--box-shadow-submenu: 0 10px 40px #00000020;
		
		--text-transform-submenu: none;
		--font-size-submenu: var(--font-size-2);
		--line-height-submenu: 2.2;
		
		--font-family-submenu: 'DM Sans', sans-serif; 
		--font-weight-submenu: 400; 
		
		// Ad header 
		
		// Header Hero 
		
		// Ad home 1 
		
		// Featured posts 
		
		// Ad home 2
		
		// Page header 
		
		// Archive category header 
		
		// Search results header 
		
		// Post header 
		
		// Content 
		
		--main-content-width: 66.66666666%; 
		--sidebar-width: 33.33333333%;
		
		// Cards

		--card-aspect-ratio-hero: 125%;																		// value used for padding-top to create ratio effect for post previews
		--card-aspect-ratio-square: 125%;
		--card-aspect-ratio-portrait: 125%;
		--card-aspect-ratio-landscape: 125%;
		
		--background-color-card-1-title: #ffffff;
		--background-color-card-2-3-content: linear-gradient(to top, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0));
		--background-color-card-6-content: #ffffff;
		--background-color-card-7-content: #ffffff;

		// Share 
		
		// Author Info 
		
		// Related posts 
		
		// Comments 
		
		// Readtime 
		
		--background-color-scroll-progress: var(--color-accent-2);
		--z-index-scroll-progress: 9999999;
		
		// Footer Top
		
		--background-color-footer-top: var(--background-color-body);
		
		// Footer 
		
		--background-color-footer: var(--background-color-body);
		
		// Footer Bottom
		
		--background-color-footer-bottom: var(--background-color-body);
		
		// Backlink
		
		--background-color-backlink: var(--background-color-body);
		
		// Mobile menu 
		
		--background-color-site-overlay: rgba(0, 0, 0, 0.75);
		--background-color-mobile-menu-wrap: var(--background-color-body);
		--z-index-site-overlay: 10000;
		--z-index-mobile-menu-wrap: 10001;
		--text-transform-mobile-menu: uppercase;
		--text-transform-mobile-submenu: none;
		
		--font-family-mobile-menu: 'DM Sans', sans-serif;
		--font-weight-mobile-menu: 700;
		
		--font-family-mobile-submenu: 'DM Sans', sans-serif;
		--font-weight-mobile-submenu: 400;
		
		// Back to top 
		
		--z-index-back-to-top: 1010;
		
	}
	
	.rb-dark-mode {

		// Colors

		--color-accent-1: #2E2D4D;
		--color-accent-2: #D50747;
		--color-muted: #555;

		--color-text: #999;
		--color-headings: #fff;
		
		--border-color: #494949;
		--background-color: #333333; 

		--color-form-elements: #777;
		--color-form-placeholders: #666;
		--color-form-labels: #777;
		
		--background-color-form-elements: #1c1c1c;
		--background-color-form-elements-disabled: #222222;
		
		--border-color-form-elements: #494949;
		--border-color-form-elements-focus: #494949;
			
		--color-link: #ffffff;
		--color-link-hover: #f8f8f8;
		--color-link-active: #f8f8f8;
		
		// Body
		
		--background-color-body: #1c1c1c; 
		
		// Cta 
		
		--color-cta: #ffffff; 
		--color-cta-hover: #ffffff;

		--background-color-cta: var(--color-accent-1);
		--border-color-cta: var(--color-accent-1);
		
		--background-color-cta-button: var(--color-accent-2);
		--color-cta-button: #ffffff;
		
		// Header Top
		
		--background-color-header-top: var(--background-color-body);
		
		// Header 
		
		--background-color-header: var(--background-color-body);
		--box-shadow-header: 0px 0px 15px #0000001a;
		
		--color-logo: var(--color-accent-2);

		--color-header-icons: var(--color-headings); 
		--color-header-icons-hover: var(--color-accent-2);
		
		--color-menu: var(--color-headings);
		--color-menu-hover: var(--color-accent-2);

		--color-submenu: var(--color-text);
		--color-submenu-hover: var(--color-accent-2);
		--background-color-submenu: #333333;  
		--border-color-submenu: transparent;
		--box-shadow-submenu: 0 10px 40px #00000020;
		
		// Cards

		--background-color-card-1-title: #444;
		--background-color-card-2-3-content: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
		--background-color-card-6-content: #444;
		--background-color-card-7-content: #444;

		// Readtime 
		
		--background-color-scroll-progress: var(--color-accent-2);
		
		// Footer Top
		
		--background-color-footer-top: var(--background-color-body); 
		
		// Footer 
		
		--background-color-footer: var(--background-color-body); 
		
		// Footer Bottom
		
		--background-color-footer-bottom: var(--background-color-body); 
		
		// Backlink
		
		--background-color-backlink: var(--background-color-body); 
		
		// Mobile Menu
		
		--background-color-site-overlay: rgba(0, 0, 0, 0.25);
		--background-color-mobile-menu-wrap: var(--background-color-body);
		
	}

	@media (min-width: 768px) {
		
		:root {
			
			// Typography
			
			--font-scale: 1.125; // 1.125 – Major Second, 1.200 – Minor Third, 1.250 – Major Third 
			
			// Cards
			
			--card-aspect-ratio-hero: 42.553%;	
			--card-aspect-ratio-square: 100%;
			--card-aspect-ratio-portrait: 150%;
			--card-aspect-ratio-landscape: 66.667%;
			
		}
		
		.rb-grid.cols-2,
		.rb-grid.cols-3,
		.rb-grid.cols-4,
		.rb-masonry-grid.cols-2,
		.rb-masonry-grid.cols-3,
		.rb-masonry-grid.cols-4 {
			--card-aspect-ratio-hero: 100%;	
			--card-aspect-ratio-landscape: 100%;
		}
		
		.rb-grid.cols-3.mixed .rb-grid-item:nth-child(3n) {
			--card-aspect-ratio-hero: 42.553%;
			--card-aspect-ratio-landscape: 66.667%;			
		}
		
	}
	
	@media (min-width: 992px) {
		
		:root,
		.rb-grid.cols-2,
		.rb-grid.cols-3,
		.rb-grid.cols-4,
		.rb-masonry-grid.cols-2,
		.rb-masonry-grid.cols-3,
		.rb-masonry-grid.cols-4 {
			
			// Cards
			
			--card-aspect-ratio-hero: 66.667%;	
			--card-aspect-ratio-square: 100%;
			--card-aspect-ratio-portrait: 150%;
			--card-aspect-ratio-landscape: 66.667%;
			
		}
		
		.rb-grid.cols-3.mixed .rb-grid-item:nth-child(3n) {
			--card-aspect-ratio-hero: 42.553%;
			--card-aspect-ratio-landscape: 66.667%;
		}
		
		
	}
	
	@media (min-width: 1200px) {
		
		:root,
		.rb-grid.cols-1,
		.rb-grid.cols-2,
		.rb-grid.cols-3,
		.rb-grid.cols-4,
		.rb-masonry-grid.cols-1,
		.rb-masonry-grid.cols-2,
		.rb-masonry-grid.cols-3,
		.rb-masonry-grid.cols-4,
		.rb-grid.cols-3.mixed .rb-grid-item:nth-child(3n) {

			--card-aspect-ratio-hero: 42.553%;	
			--card-aspect-ratio-square: 100%;
			--card-aspect-ratio-portrait: 150%;
			--card-aspect-ratio-landscape: 66.667%;
			
		}
		
	}
